import { CONSTS } from "./consts.js";
import { elements, filter } from "./dom-elements.js";
import { templates } from "./templates.js";
import { renderElements, removeElementClass } from "./utils.js";

const responseSuccess = (response) => {
  const picturesData = response;
  const pictureElementBlocks = createPictureElements(picturesData);

  renderElements(elements.picturesContainer, pictureElementBlocks);
  removeElementClass(filter.container, CONSTS.FILTER_HIDDEN);

  return picturesData;
};

const responseError = (errorMessage) => {
  renderElements(elements.main, [templates.loadMessages]);
  const loadError = document.querySelector(".img-upload__message");
  removeElementClass(loadError, CONSTS.MODAL_LOAD);
  loadError.textContent = `${errorMessage}`;
};

const createPictureElements = (response) => {
  return response.map((item) => {
    const { id, url, likes, comments } = item;
    const pictureElement = templates.fetchPicture.cloneNode(true);

    pictureElement.setAttribute("id", id);
    pictureElement.querySelector(".picture__img").src = url;
    pictureElement.querySelector(".picture__likes").textContent = likes;
    pictureElement.querySelector(".picture__comments").textContent =
      comments.length;

    return pictureElement;
  });
};

export { createPictureElements, responseSuccess, responseError };
