import { CONSTS } from "./components/consts.js";
import {
  responseSuccess,
  responseError,
} from "./components/response-handler.js";
import { loadData } from "./components/recieve-data.js";
import { previewHandler } from "./components/preview.js";
import { picturesFilter } from "./components/pictures-filter.js";
import { uploadHandler } from "./components/image-upload.js";

loadData(CONSTS.LOAD_URL)
  .then(responseSuccess, responseError)
  .then(previewHandler)
  .then(picturesFilter)
  .then(uploadHandler);
