import { CONSTS } from "./consts.js";
import { upload, slider } from "./dom-elements.js";

const onPinMousedown = (evt) => {
  evt.preventDefault();

  const start = new Coords(evt.clientX);
  const selectedEffect = getSelectedEffect();

  const onMousemove = (moveEvt) => {
    const shift = new Coords(start.x - moveEvt.clientX);
    const pinX = slider.pin.offsetLeft - shift.x;
    const proportion = Math.floor(
      (pinX / (slider.bar.offsetWidth - slider.pin.offsetWidth)) *
        CONSTS.SLIDER_MAX_VALUE
    );
    start.x = moveEvt.clientX;

    if (!(pinX < 0 || pinX > slider.bar.offsetWidth)) {
      slider.pin.style.left = `${pinX}px`;
      slider.depth.style.width = `${pinX}px`;
      slider.effectLevel.setAttribute(
        "value",
        `${
          proportion > CONSTS.SLIDER_MAX_VALUE
            ? CONSTS.SLIDER_MAX_VALUE
            : proportion
        }`
      );
      setEffectDepth(selectedEffect, proportion);
    }
  };

  const onMouseup = () => {
    document.removeEventListener("mousemove", onMousemove);
    document.removeEventListener("mouseup", onMouseup);
  };

  document.addEventListener("mousemove", onMousemove);
  document.addEventListener("mouseup", onMouseup);
};

const getSelectedEffect = () => {
  return upload.imagePreview.getAttribute("class");
};

const setEffectDepth = (effectName, proportion) => {
  const effectProp = proportion / CONSTS.EFFECT_PROPORTION;
  const { style } = upload.imagePreview;

  switch (effectName) {
    case "effects__preview--chrome":
      style.filter = `grayscale(${effectProp})`;
      break;
    case "effects__preview--sepia":
      style.filter = `sepia(${effectProp})`;
      break;
    case "effects__preview--marvin":
      style.filter = `invert(${proportion}%)`;
      break;
    case "effects__preview--phobos":
      style.filter = `blur(${
        effectProp * CONSTS.PHOBOS_MAX_VALUE > CONSTS.PHOBOS_MAX_VALUE
          ? CONSTS.PHOBOS_MAX_VALUE
          : effectProp * CONSTS.PHOBOS_MAX_VALUE
      }px)`;
      break;
    case "effects__preview--heat":
      style.filter = `brightness(${
        effectProp * CONSTS.HEAT_MAX_VALUE < CONSTS.HEAT_MIN_VALUE
          ? CONSTS.HEAT_MIN_VALUE
          : effectProp * CONSTS.HEAT_MAX_VALUE
      })`;
      break;
    default:
      upload.imagePreview.removeAttribute("style");
      break;
  }
};

const setSliderDefaults = () => {
  slider.pin.style.left = `${CONSTS.SLIDER_MAX_VALUE}%`;
  slider.depth.style.width = `${CONSTS.SLIDER_MAX_VALUE}%`;
  slider.effectLevel.setAttribute("value", `${CONSTS.SLIDER_MAX_VALUE}`);
  upload.imagePreview.removeAttribute("style");
};

const Coords = function (x) {
  this.x = x;
};

export { setSliderDefaults, onPinMousedown };
