import { CONSTS } from "./consts.js";
import {
  elements,
  upload,
  scale,
  slider,
  effects,
  form,
} from "./dom-elements.js";
import { addElementClass, removeElementClass } from "./utils.js";
import { onScaleClick, setDefaultScaleValue } from "./scale.js";
import { setDefaulEffectValue, onEffectClick } from "./effects.js";
import { onPinMousedown } from "./effect-slider.js";
import {
  onFormSubmitClick,
  onInputKeyup,
  setFormDefaults,
} from "./form-validation.js";

const uploadHandler = () => {
  const onUploadInputChange = (evt) => {
    evt.preventDefault();
    setImageFromFile();
    removeElementClass(upload.imageOverlay, CONSTS.HIDDEN);
    addElementClass(elements.body, CONSTS.MODAL);
    addElementClass(slider.container, CONSTS.HIDDEN);
    scale.container.addEventListener("click", onScaleClick);
    slider.pin.addEventListener("mousedown", onPinMousedown);
    effects.container.addEventListener("click", onEffectClick);
    form.container.addEventListener("focusin", onInputFocusin);
    form.container.addEventListener("focusout", onInputFocusout);
    form.hashInput.addEventListener("keyup", onInputKeyup);
    form.submit.addEventListener("click", onFormSubmitClick);
    document.addEventListener("keydown", onEscKeydown);
    upload.closePreview.addEventListener("click", onClosePreviewButtonClick);
  };

  upload.imageUpload.addEventListener("change", onUploadInputChange);
};

const onEscKeydown = (evt) => {
  if (evt.key === "Escape") {
    evt.preventDefault();
    setImageUploadDefaults();
  }
};

const onClosePreviewButtonClick = (evt) => {
  evt.preventDefault();
  setImageUploadDefaults();
};

const setImageFromFile = () => {
  const file = upload.imageUpload.files[0];
  upload.imagePreview.src = URL.createObjectURL(file);

  const onUploadPreviewLoad = () => {
    URL.revokeObjectURL(upload.imagePreview.src);

    upload.imagePreview.removeEventListener("load", onUploadPreviewLoad);
  };

  upload.imagePreview.addEventListener("load", onUploadPreviewLoad);
};

const resoreFileInputValue = () => {
  upload.imageUpload.value = "";
};

// TODO REFACTOR BELOW FUNCTIONS
const onInputFocusin = (evt) => {
  const input = evt.target.closest(".text__hashtags");
  const textarea = evt.target.closest(".text__description");

  if (!input && !textarea) return;

  if (input || textarea) {
    document.removeEventListener("keydown", onEscKeydown);
  }
};

const onInputFocusout = (evt) => {
  const input = evt.target.closest(".text__hashtags");
  const textarea = evt.target.closest(".text__description");

  if (!input && !textarea) return;

  if (input || textarea) {
    document.addEventListener("keydown", onEscKeydown);
  }
};

const setImageUploadDefaults = () => {
  addElementClass(upload.imageOverlay, CONSTS.HIDDEN);
  removeElementClass(elements.body, CONSTS.MODAL);
  resoreFileInputValue();
  setDefaultScaleValue();
  setDefaulEffectValue();
  setFormDefaults();
  scale.container.removeEventListener("click", onScaleClick);
  slider.pin.removeEventListener("mousedown", onPinMousedown);
  effects.container.removeEventListener("click", onEffectClick);
  form.container.removeEventListener("focusin", onInputFocusin);
  form.container.removeEventListener("focusout", onInputFocusout);
  form.hashInput.removeEventListener("keyup", onInputKeyup);
  form.submit.removeEventListener("click", onFormSubmitClick);
  document.removeEventListener("keydown", onEscKeydown);
  upload.closePreview.removeEventListener("click", onClosePreviewButtonClick);
};

export { uploadHandler, setImageUploadDefaults };
