import { CONSTS } from "./consts.js";
import { elements, preview } from "./dom-elements.js";
import {
  addElementClass,
  clearContainer,
  removeElementClass,
} from "./utils.js";
import {
  renderComments,
  showCommentsLoaderButton,
  hideCommentsLoaderButton,
  commentsConfig,
} from "./comments.js";

const previewHandler = (picturesData) => {
  const onPictureClick = (evt) => {
    const picture = evt.target.closest(".picture");

    if (!picture) return;
    if (picture) {
      evt.preventDefault();

      const clickedPictureData = getPictureDataById(picture, picturesData);
      setPreviewInfo(clickedPictureData);
      removeElementClass(elements.fullsceenPicture, CONSTS.HIDDEN);
      addElementClass(elements.body, CONSTS.MODAL);
      elements.closePreview.addEventListener(
        "click",
        onPreviewCloseButtonClick
      );
      document.addEventListener("keydown", onDocumentKeydown);
    }
  };

  elements.picturesContainer.addEventListener("click", onPictureClick);

  return picturesData;
};

const getPictureDataById = (element, pictures) => {
  const id = parseInt(element.getAttribute("id"), 10);
  return pictures.find((picture) => picture.id === id);
};

const setPreviewInfo = (clickedPictureData) => {
  const { url, likes, comments, description } = clickedPictureData;
  const commentsStart = comments.length > 5 ? 5 : comments.length;

  preview.bigPicture.src = url;
  preview.caption.textContent = description;
  preview.commentsFrom.textContent = commentsStart;
  preview.commentsTo.textContent = comments.length;
  preview.likes.textContent = likes;

  clearContainer(elements.commentsList);
  commentsConfig.toRender = comments.slice();
  renderComments(commentsConfig.toRender.splice(0, commentsConfig.STEP));

  if (comments.length <= commentsConfig.STEP) {
    hideCommentsLoaderButton();
  } else {
    showCommentsLoaderButton();
  }
};

const onPreviewCloseButtonClick = (evt) => {
  evt.preventDefault();

  addElementClass(elements.fullsceenPicture, CONSTS.HIDDEN);
  removeElementClass(elements.body, CONSTS.MODAL);
  commentsConfig.toRender = [];
  commentsConfig.counter = 0;
  document.removeEventListener("keydown", onDocumentKeydown);
  elements.picturesContainer.removeEventListener(
    "click",
    onPreviewCloseButtonClick
  );
};

const onDocumentKeydown = (evt) => {
  if (evt.key === "Escape") {
    evt.preventDefault();

    addElementClass(elements.fullsceenPicture, CONSTS.HIDDEN);
    removeElementClass(elements.body, CONSTS.MODAL);
    commentsConfig.toRender = [];
    commentsConfig.counter = 0;
    document.removeEventListener("keydown", onDocumentKeydown);
    elements.picturesContainer.removeEventListener(
      "click",
      onPreviewCloseButtonClick
    );
  }
};

export { previewHandler };
