import { CONSTS } from "./consts.js";
import { elements, preview } from "./dom-elements.js";
import {
  renderAdjacent,
  addElementClass,
  removeElementClass,
} from "./utils.js";

const commentsConfig = {
  STEP: 5,
  toRender: [],
  counter: 0,
};

const createCommentMarkup = (commentData) => {
  const { avatar, message, name } = commentData;

  return `<li class="social__comment">
          <img
          class="social__picture"
          src="${avatar}"
          alt="${name}"
          width="35" height="35">
          <p class="social__text">${message}</p>
          </li>`;
};

const renderComments = (commentsData) => {
  const commentsBlockElements = commentsData.map((comment) =>
    createCommentMarkup(comment)
  );

  renderAdjacent(elements.commentsList, commentsBlockElements.join("\n"));
  commentsConfig.counter += commentsData.length;
  showCommentsLoadedCount(commentsConfig.counter);
};

const showCommentsLoadedCount = (count) => {
  preview.commentsFrom.textContent = count;
};

const showCommentsLoaderButton = () => {
  removeElementClass(elements.commentsLoader, CONSTS.HIDDEN);

  elements.commentsLoader.addEventListener(
    "click",
    onCommentsLoaderButtonClick
  );
};

const hideCommentsLoaderButton = () => {
  addElementClass(elements.commentsLoader, CONSTS.HIDDEN);

  elements.commentsLoader.removeEventListener(
    "click",
    onCommentsLoaderButtonClick
  );
};

const onCommentsLoaderButtonClick = (evt) => {
  evt.preventDefault();

  if (commentsConfig.toRender.length <= commentsConfig.STEP) {
    hideCommentsLoaderButton();
  }

  renderComments(commentsConfig.toRender.splice(0, commentsConfig.STEP));
};

export {
  renderComments,
  showCommentsLoaderButton,
  hideCommentsLoaderButton,
  commentsConfig,
};
