import { CONSTS } from "./consts.js";
import { upload, effects, slider } from "./dom-elements.js";
import { addElementClass, removeElementClass } from "./utils.js";
import { setSliderDefaults } from "./effect-slider.js";
import { setDefaultScaleValue } from "./scale.js";

const EffectsMap = {
  chrome: "effects__preview--chrome",
  sepia: "effects__preview--sepia",
  marvin: "effects__preview--marvin",
  phobos: "effects__preview--phobos",
  heat: "effects__preview--heat",
  none: "effects__preview--none",
};

const onEffectClick = (evt) => {
  const effect = evt.target.closest(".effects__radio");

  if (!effect) return;
  const effectId = clearEffectName(effect, "effect-");
  upload.imagePreview.className = EffectsMap[effectId];
  hideSliderByDefault(effect);
  setDefaultScaleValue();
  setSliderDefaults();
};

const clearEffectName = (effectName, toClear) => {
  return effectName.getAttribute("id").replace(`${toClear}`, "");
};

const hideSliderByDefault = (effect) => {
  if (effect.hasAttribute("checked")) {
    addElementClass(slider.container, CONSTS.HIDDEN);
  } else {
    removeElementClass(slider.container, CONSTS.HIDDEN);
  }
};

const setDefaulEffectValue = () => {
  effects.default.checked = true;
  effects.default.focus();
  upload.imagePreview.removeAttribute("class");
  upload.imagePreview.removeAttribute("style");
  slider.effectLevel.setAttribute("value", CONSTS.SLIDER_DEFAULT_VALUE);
};

export { setDefaulEffectValue, onEffectClick };
