import { CONSTS } from "./consts.js";
import {
  addElementClass,
  clearAllElementsClass,
  renderElements,
  getRandomInteger,
  debounce,
} from "./utils.js";
import { elements, filter } from "./dom-elements.js";
import { createPictureElements } from "./response-handler.js";

const picturesFilter = (picturesData) => {
  const onFilterClick = (evt) => {
    evt.preventDefault();

    const filterName = evt.target.getAttribute("id");
    const filterButton = evt.target.closest(".img-filters__button");
    const pictureElements = document.querySelectorAll(".picture");
    const data = picturesData.slice();

    if (!filterName && !filterButton) return;

    if (filterButton) {
      clearAllElementsClass(filter.buttons, CONSTS.FILTER_ACTIVE);
      addElementClass(filterButton, CONSTS.FILTER_ACTIVE);
    }

    const filterDefault = () => {
      const filteredByDefault = createPictureElements(data);

      removePictureElements(pictureElements);
      renderElements(elements.picturesContainer, filteredByDefault);
    };

    const filterRandom = () => {
      const getRandom = () => data[getRandomInteger(data.length)];
      const random = new Array(data.length).fill("").map(getRandom);
      const randomUnique = Array.from(new Set(random)).slice(
        0,
        CONSTS.FILTER_RANDOM_MAX
      );
      const randomElements = createPictureElements(randomUnique);

      removePictureElements(pictureElements);
      renderElements(elements.picturesContainer, randomElements);
    };

    const filterDiscussed = () => {
      const getMostDiscussed = (a, b) => b.comments.length - a.comments.length;
      const descending = data.slice().sort(getMostDiscussed);
      const descendingElements = createPictureElements(descending);

      removePictureElements(pictureElements);
      renderElements(elements.picturesContainer, descendingElements);
    };

    if (filterName) {
      switch (filterName) {
        case CONSTS.FILTER_DEFAULT:
          debounce(filterDefault, CONSTS.DEBOUNCE_INTERVAL)();
          break;
        case CONSTS.FILTER_RANDOM:
          debounce(filterRandom, CONSTS.DEBOUNCE_INTERVAL)();
          break;
        case CONSTS.FILTER_DISCUSSED:
          debounce(filterDiscussed, CONSTS.DEBOUNCE_INTERVAL)();
          break;
      }
    }
  };

  filter.container.addEventListener("click", onFilterClick);
};

const removePictureElements = (elements) => {
  for (const element of elements) {
    element.remove();
  }
};

export { picturesFilter };
