import { scale, upload } from "./dom-elements.js";

const ScaleMap = {
  STEP: 25,
  MIN_VALUE: 25,
  MAX_VALUE: 100,
  DEFAULT_VALUE: 100,
  PROPORTION: 100,
};

const onScaleClick = (evt) => {
  evt.preventDefault();
  const buttons = {
    dec: evt.target.closest(".scale__control--smaller"),
    inc: evt.target.closest(".scale__control--bigger"),
  };

  if (!buttons.dec && !buttons.inc) return;

  if (buttons.dec) {
    setScaleValue(buttons.dec);
  }

  if (buttons.inc) {
    setScaleValue(buttons.inc);
  }
};

const setScaleValue = (control) => {
  const scaleValue = parseInt(scale.valueInput.value, 10);

  if (control.classList.contains("scale__control--smaller")) {
    const minValue =
      scaleValue > ScaleMap.MIN_VALUE
        ? scaleValue - ScaleMap.STEP
        : ScaleMap.MIN_VALUE;

    scale.valueInput.value = `${minValue}%`;
    scale.valueInput.setAttribute("value", `${minValue}%`);

    upload.imagePreview.style.transform = `scale(${
      minValue / ScaleMap.PROPORTION
    })`;
  }

  if (control.classList.contains("scale__control--bigger")) {
    const maxValue =
      scaleValue < ScaleMap.MAX_VALUE
        ? scaleValue + ScaleMap.STEP
        : ScaleMap.MAX_VALUE;

    scale.valueInput.value = `${maxValue}%`;
    scale.valueInput.setAttribute("value", `${maxValue}%`);

    upload.imagePreview.style.transform = `scale(${
      maxValue / ScaleMap.PROPORTION
    })`;
  }
};

const setDefaultScaleValue = () => {
  scale.valueInput.value = `${ScaleMap.DEFAULT_VALUE}%`;
  scale.valueInput.setAttribute("value", `${ScaleMap.DEFAULT_VALUE}%`);
  upload.imagePreview.removeAttribute("style");
};

export { onScaleClick, setDefaultScaleValue };
